a {
  color: inherit;
  font-size: 1.8vw;
  font-weight: bold;
  text-decoration: none;
}

a:hover, a:active {
  color: #3354f8;
}

.back-link {
  color: inherit;
  align-items: center;
  padding-left: 4vw;
  font-weight: bold;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.back-link:hover {
  color: #3354f8;
}

.back-link:before {
  content: "←";
  font-size: 3.8vw;
  transition: color .3s;
  animation: 3s infinite moveArrow;
  position: absolute;
  top: -3.2vh;
  left: 0;
}

@keyframes moveArrow {
  0% {
    left: -7vw;
  }

  50% {
    left: 0;
  }

  51% {
    left: 0;
  }

  100% {
    left: -7vw;
  }
}

body {
  background-color: #f0f0f0;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
}

.positionPromises {
  justify-content: center;
  align-items: center;
  display: flex;
}

.form {
  background-color: #fff;
  border-radius: 1vw;
  width: 80vw;
  max-width: 600px;
  padding: 2vw;
  box-shadow: 0 .5vw 1vw #0000001a;
}

.form label {
  flex-direction: column;
  margin-bottom: 2vh;
  font-size: 1.5vw;
  display: flex;
}

.form input {
  border: 1px solid #ccc;
  border-radius: .5vw;
  margin-top: .5vh;
  padding: .5vw;
  font-size: 1.5vw;
}

.form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: .5vw;
  width: 100%;
  padding: 1vh;
  font-size: 1.5vw;
  transition: background-color .3s;
}

.form button:hover {
  background-color: #518554;
}
